.footer-list {
  box-sizing: border-box;
  padding-left: 6.25%;
  padding-right: 6.25%;
  width: 100%;
  background-color: #f2f2f2;
  list-style: none;
  margin: 0;
}

.footer-list__item {
  box-sizing: border-box;
  width: 100%;
}

.footer-list__item:first-of-type {
  border-bottom: 1px solid #cdcdcd;
  height: 94px;
}

.footer-list__item:nth-of-type(2) {
  height: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding-left: 26%;
  padding-right: 29.3%;
}

.footer-list__item:last-of-type {
  border-top: 1px solid #cdcdcd;
  height: 78px;
}

.footer-list__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}

.footer-list__logo-image {
  width: 45.7%;
  height: 24px;
  padding-top: 39px;
  padding-left: 27.14%;

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}

.footer-list__vk-icon {
  width: 22px;
  height: 14px;
  color: #666666;

  &:active {
    opacity: 30%;
  }
}

.footer-list__insta-icon {
  width: 16px;
  height: 18px;
  margin-right: 5px;
  margin-top: 4px;
  color: #666666;

  &:active {
    opacity: 30%;
  }
}

.footer-list__fb-icon {
  width: 10px;
  height: 19px;
  color: #666666;

  &:active {
    opacity: 30%;
  }
}

.footer-list__link:hover svg {
  color: #68b738;
}

.footer-list__copyright {
  text-decoration: none;
  font-family: "ArialMT", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  text-align: left;
  color: #444444;

  &:active {
    opacity: 30%;
  }
}

.footer-list__html-academy-logo {
  margin-top: 6px;
  margin-right: -7px;
  color: #666666;

  &:active {
    opacity: 30%;
  }
}

@media (min-width: $tablet-width) {
  .footer-list {
    padding-left: 31px;
    padding-right: 30px;
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
  }

  .footer-list__item:first-of-type,
  .footer-list__item:nth-of-type(2) {
    height: 34px;
  }

  .footer-list__item:nth-of-type(2) {
    padding-left: 4.5%;
    padding-right: 0;
    justify-content: center;
    width: 33%;
    flex-wrap: wrap;
  }

  .footer-list__item:first-of-type {
    border-bottom: none;
    padding-top: 6px;
    width: 128px;
  }

  .footer-list__item:last-of-type {
    border-top: none;
    width: 176px;
    height: 56px;
    padding-bottom: 3px;
  }

  .footer-list__item div {
    padding: 0 13px;
  }

  .footer-list__vk-icon {
    width: 30px;
    height: 16px;
  }

  .footer-list__insta-icon {
    width: 23px;
    height: 23px;
    margin-top: 0;
    padding-left: 3px;
  }

  .footer-list__fb-icon {
    width: 14px;
    height: 28px;
  }

  .footer-list__logo-image {
    padding: 0;
    width: 128px;
    height: 24px;
  }

  .footer-list__html-academy-logo {
    margin-top: 6px;
    margin-right: -7px;
    width: 42px;
    height: 46px;
  }
}

@media (min-width: $desktop-width) {
  .footer-list {
    box-sizing: border-box;
    width: 1300px;
    padding-left: 40px;
    padding-right: 40px;
    margin: 0 auto;
    height: 150px;
  }

  .footer-list__item {
    height: 34px;
  }

  .footer-list__item:first-of-type {
    padding-top: 5px;
  }

  .footer-list__item:nth-of-type(2) {
    padding-left: 30px;
    width: 33%;
    padding-bottom: 2px;
  }

  .footer-list__item:last-of-type {
    width: 178px;
    height: 51px;
  }

  .footer-list__html-academy-logo {
    width: 42px;
  }
}
